/* Imports */
// Icons
@import '../icons/B2BCapitalIcons-v1.0/style.scss';
// Buttons
@import 'base/buttons';
// Colors
@import 'base/variables';
// Spacing
@import 'base/spacing';
// Fonts
@import 'base/typography';
// Mixins
@import 'base/mixins';
// Forms
@import 'base/forms';
// Animations
@import 'base/animations';
@import './pages/pages.scss';
/* Some Resets */
h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	padding: 0;
}
a {
	&:link,
	&:hover,
	&:visited,
	&:active {
		text-decoration: none;
	}
}
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
/* HTML Styles Start Here */
html {
	width: 100vw;
	height: 100vh;
	font-size: 10px;
	@include normal-screen {
		font-size: 14px;
	}
	@include large-screen {
		font-size: 16px;
	}
}
/* Body Styles Start Here */
body {
	direction: ltr;
	width: 100%;
	height: 100%;
	overflow: hidden;
	color: $primary-dark;
	line-height: 1.5;
	font-family: $bbc-primary-en-font-regular;

	/* Body Arabic Styles Start Here */
	&.rtl {
		direction: rtl;
		text-align: right;
	}
}
/***** All Scrollbars Styles Start Here *****/
/* Any Scrollbar Width & Height */
::-webkit-scrollbar {
	width: 2px;
	height: 2px;
}
/* Any Scrollbar Track Color */
::-webkit-scrollbar-track {
	//border-radius: 2px;
	background: rgba($layout-medium, 0.25);
}
/* Vertical Scrollbar Handle Color */
::-webkit-scrollbar-thumb {
	//border-radius: 2px;
	background: $primary;
}
/* Vertical Scrollbar Handle Color On Hover */
::-webkit-scrollbar-thumb:hover {
	background: darken($primary, 15%);
}

.app-brand {
	display: inline-block;
	width: 5rem;
	margin-bottom: 1.5rem;
	@include large-screen {
		width: 8.8rem;
	}
}
.section-header{
	margin-bottom: 3rem;
}
.section-title{
	font-family: $bbc-primary-en-font-regular;
	font-size: 34px;
}
